@charset "UTF-8";
.slideshow {
  position: relative; }
  .slideshow:hover .slideshow__nav {
    visibility: visible; }
  .slideshow__slides {
    width: 100%; }
  .slideshow__nav {
    visibility: hidden;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; }
  .slideshow .slides-nav {
    background-color: rgba(0, 0, 0, 0.15);
    height: 100%;
    position: absolute;
    top: 0;
    width: 36px;
    z-index: 10;
    display: table;
    cursor: pointer; }
    .slideshow .slides-nav:hover {
      background-color: rgba(0, 0, 0, 0.35); }
    .slideshow .slides-nav:after {
      display: table-cell;
      vertical-align: middle;
      text-align: center; }
    .slideshow .slides-nav--prev {
      left: 0; }
      .slideshow .slides-nav--prev:after {
        content: '«';
        color: white; }
    .slideshow .slides-nav--next {
      right: 0; }
      .slideshow .slides-nav--next:after {
        content: '»';
        color: white; }
  .slideshow .slide {
    display: none; }
    .slideshow .slide--active {
      display: block; }
  .slideshow .pagination {
    width: 100%;
    float: left;
    text-align: center; }
    .slideshow .pagination__item {
      display: inline-block;
      width: 10px;
      height: 10px;
      background-color: rgba(0, 0, 0, 0.15);
      border-radius: 50%;
      margin: 0 20px;
      text-indent: -9999px;
      cursor: pointer; }
      .slideshow .pagination__item--active {
        background-color: rgba(0, 0, 0, 0.35); }



